.nav-tabs {
    width: 100%;
    float: left;
    margin: 0;
    list-style-type: none;
}

.nav-tabs>li {
    float: left;
    margin-bottom: -1px;
}

.nav-tabs>li>a {
    text-decoration: none;
    line-height: 1.2;
    padding: 1em;
    float: left;
}

.nav-tabs>li>a:hover {
    border-color: #eee #eee #ddd;
}

.nav-tabs>li.active a,
.nav-tabs>li.active a:hover,
.nav-tabs>li.active a:focus {
    color: black;
    background-color: #fff;
    cursor: pointer;
    border: 1px solid;
}

.tab-content>.tab-pane {
    float: left;
    width: 100%;
    display: none;
}

.tab-content>.tab-pane.active {
    display: block;
    padding: 12px;
    background-color: #fff;

}